<script>
import Header from "../../components/global/header/HeaderArea.vue"
import { ref, onMounted, onUnmounted } from 'vue'
import NoData from "@/components/global/NoData.vue"
import { apiGetCareRecipientList } from "@/api/api"
import { isBetweenBottom } from "@/utils/helper"


export default {
  setup() {
    const careRecipientData = ref([])
    const page = ref(1)
    let totalData = 0
    let getApiTimer = null
    onMounted(async () => {
      // call api
      careRecipientData.value = await apiGetCareRecipientList({ page: page.value, limit: 30 })
      totalData = careRecipientData.value.data.total
      careRecipientData.value = careRecipientData.value.data.data

      //每0.5S監測是否在底部
      getApiTimer = setInterval(() => {
        if (isBetweenBottom()) {
          // 超過上限不做GET
          if (careRecipientData.value.length < totalData) {
            handleScroll();
          }
        }
      }, 500)
    })

    //滾動載入資料
    const handleScroll = async () => {
      page.value += 1;
      let newData = ref()
      newData.value = await apiGetCareRecipientList({ page: page.value, limit: 30 })
      careRecipientData.value = careRecipientData.value.concat(newData.value.data.data);
    };
    //取消監控
    onUnmounted(() => clearInterval(getApiTimer))

    return { careRecipientData, page };
  },
  components: { NoData,Header },
};

</script>

<template>
  <Header title="被照顧者資料" right="add" path="/careRecipient/add" backToPath="/home" />
  <div class="careRecipientList">
    <ul class="list-group list-group-flush">
      <NoData v-if="careRecipientData.length == 0" />
      <div class="listItem" v-for="item in careRecipientData" :key="item.id">
        <button type="button" class="list-group-item list-group-item-action"
          @click="$router.push({ path: `/careRecipient/info/${item.id}` })">{{ item.name }}
          ({{ item.gender == "Female" ? "女性" : "男性" }}/{{ item.age }}歲/{{ item.height }}cm/{{ item.weight
          }}kg)
        </button>
      </div>
    </ul>
  </div>
</template>

<style lang="scss" scoped>
@import "@/assets/custom/config";


.careRecipientList {
  margin-top: $header-height;
  margin-bottom: calc($footer-height + 15px);
}
</style>